<script>
import {swiper,swiperSlide} from 'vue-awesome-swiper'
export default {
    data(){
        return{
            flag:0,
            activeImg:'',
            swiperOption:{
                loop:true,
                notNextTick:true,
                paginationClickable: true,
                pagination: {
                    el: '.swiper-pagination',
