                },
                {
                    "imgUrl":"../../../static/wufuheader/partner3.png",
                    "imgUrlActive":"../../../static/wufuheader/partner3-active.png"
                },
                {
                    "imgUrl":"../../../static/wufuheader/partner4.png",
                    "imgUrlActive":"../../../static/wufuheader/partner4-active.png"
                },
